/* ============================================================= */
.header{
	padding: 20px 0;
	color: rgb(248, 103, 66);
	font-size: 26px;
	letter-spacing: 1.5px;
	text-transform: uppercase;
	font-weight: 700;
}
.header.header-dark{
	background-color: #212121;
}
.header.header-light{
	background-color: #d3d3d3;
}
/* =================================================================== */
.switch-btn{
	position: absolute;
	top: 23px;
	right: 28px;
}
.container{
	max-width: 1280px;
	margin: 0 auto;
	padding: 55px 0;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.todo-list{
	padding-top: 35px;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 2rem;
}
.todo-card{
	transition: all 0.3s ease-out;
	width: 100%;
	min-width: 280px;
	max-width: 400px;
}
.todo-card__title{
	padding: 12px 12px 5px 17px;
	font-size: 23px;
	letter-spacing: 1px;
}
.todo-card__title-login{
	font-size: 28px;
}
.todo-card__task{
	padding: 5px 12px 0px 17px;
	letter-spacing: 0.3px;
}
.calender{
	padding: 0 0 0px 12px;
}
.calender__icon{
	font-size: 1.3em;
	margin-right: 5px;
}
.checkbox-label{
	padding-right: 17px;
	cursor: pointer;
}
@media (min-width: 895px ) {
	.todo-list{
		flex: 1 0 400px;
		flex-direction: row;
		flex-wrap: wrap;
		/* justify-content: center; */
	}
}
@media (min-width: 906px ) {
	.container{
		align-items: start;
		padding-top: 15px;
	}
}
label{
	margin:  0;
	padding-left: 12px;
	font-size: 20px;
	display: block;
	text-align: left;
}
.form-card{
	padding: 0;
	text-align: left;
	background-color: hsla(0, 0%, 27%, 0.242);
}
.input-container{
	position: relative;

}
input{
	width: 100%;
	padding: 5px 10px;
	background-color: transparent;
	transition: background-color 0.3s ease-out;
}
input:hover{
	background-color: #ffffff09;
}
.input-container::after{
	position: absolute;
	content: '';
	width: 100%;
	height: 1px;
	background-color: #fff;
	bottom: 0;
	left: 0;
}
/* btns=================================================================== */
.btns {
	padding-right: 15px;
	font-size: 22px;
}
.btns:hover{
	cursor: pointer;
}
.edit-btn:hover{
	fill: #f9d579
}
.btn{
	padding-right: 12px;
	background-color: #17c664;
	transition: all ease-out 0.3s;
}
.btn:hover{
	background-color: #179a50;
}
.btn:active{
	background-color: #127b3f;
}
.btn-log{
	font-size: 22px;
	width: 100%;
	padding: 8px 0;
	border-radius: 12px;
}
/* =================================================================== */